html {
  font-size: 62, 5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Oswald", sans-serif;
  background-color: #F2E5D7;
  margin: 0;
}
body.fijar-body {
  overflow: hidden;
}

h1 {
  font-size: 2.5rem;
  font-family: "Anton", sans-serif;
}

h2 {
  font-size: 1.8rem;
}

h3 {
  font-size: 1.4rem;
}

p, button {
  font-family: "Oswald", sans-serif;
}

.container-login {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.container-login .container-nav {
  background-color: red;
}
.container-login .container-nav .container-header {
  display: grid;
  grid-template-columns: 1fr 0fr;
  gap: 1rem;
  align-items: center;
}
.container-login .container-nav .container-header h1 {
  margin: 1rem 0;
  margin-left: 2rem;
}
@media (max-width: 768px) {
  .container-login .container-nav .container-header h1 {
    font-size: 1.8rem;
  }
}
.container-login .container-nav .button-siging {
  display: flex;
  justify-content: right;
  margin-right: 2rem;
}
.container-login .container-nav .button-siging button {
  display: block;
  font-size: 1.8rem;
  width: 10rem;
  font-family: "Oswald", sans-serif;
  font-weight: bold;
  background-color: transparent;
  border: 2px solid #262322;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
@media (max-width: 768px) {
  .container-login .container-nav .button-siging button {
    font-size: 1.5rem;
    width: 5rem;
  }
}
.container-login .container-nav .button-siging button span {
  position: relative;
  z-index: 2;
}
.container-login .container-nav .button-siging button:after {
  content: "";
  width: 10rem;
  position: absolute;
  z-index: 1;
  top: 0;
  background-color: #F2E5D7;
  left: calc(-100% - 84px);
  transition: 0.5s ease-in-out all;
  height: 10rem;
}
.container-login .container-nav .button-siging button:hover:after {
  left: 0;
}
.container-login .container {
  margin: 0 auto;
  width: 30rem;
  border: 2px solid #262322;
  border-radius: 0.5rem;
}
@media (max-width: 768px) {
  .container-login .container {
    width: 25rem;
  }
}
@media (max-width: 480px) {
  .container-login .container {
    width: 95%;
    margin: 2rem auto 0 auto;
  }
}
.container-login .container h3 {
  text-align: center;
}
.container-login .container form {
  margin: 0 auto;
  width: 90%;
}
.container-login .container form .container_input {
  position: relative;
  margin: 1rem 0;
}
.container-login .container form .container_input .label-login {
  position: absolute;
  left: 1rem;
  top: 1rem;
  transition: all 0.2s;
  z-index: 1;
  color: #262322;
  padding: 0 0.2rem;
}
.container-login .container form .container_input .label-login::before {
  content: "";
  height: 0.5rem;
  background-color: #F2E5D7;
  position: absolute;
  left: 0;
  top: 1rem;
  width: 100%;
  z-index: -1;
}
.container-login .container form .container_input .input-login {
  padding: 0.8rem;
  width: 100%;
  border: 2px solid #262322;
  border-radius: 0.5rem;
  font-size: 1.3rem;
  outline: none;
  transition: all 0.3s;
  color: #262322;
  background-color: #F2E5D7;
}
.container-login .container form .container_input .input-login:focus {
  border: 3px solid;
}
.container-login .container form .container_input .input-login::placeholder {
  font-size: 1.6rem;
  opacity: 0;
  transition: all 0.3s;
}
.container-login .container form .container_input .input-login:focus::placeholder {
  opacity: 1;
}
.container-login .container form .container_input .input-login:focus + .label-login, .container-login .container form .container_input .filled {
  top: -1rem;
  font-size: 1.2rem;
}
.container-login .container form .container_input span {
  color: red;
  font-weight: bold;
}
.container-login .container form button {
  background-color: red;
  width: 100%;
  padding: 0.5rem;
  font-weight: bold;
  font-size: 1.4rem;
  cursor: pointer;
  border-radius: 0.5rem;
  margin: 0 auto 1rem auto;
}
.container-login .footer-login {
  text-align: center;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay .container {
  margin: 0 auto;
  width: 40rem;
  border: 2px solid #262322;
  border-radius: 0.5rem;
  background-color: #F2E5D7;
}
@media (max-width: 768px) {
  .overlay .container {
    width: 25rem;
  }
}
@media (max-width: 480px) {
  .overlay .container {
    width: 95%;
    margin: 2rem auto 0 auto;
  }
}
.overlay .container label {
  margin-left: 2rem;
}
.overlay .container h3 {
  text-align: center;
}
.overlay .container .container_input {
  position: relative;
  margin: 1rem 2rem;
}
.overlay .container .container_input .label-login {
  position: absolute;
  left: 1rem;
  top: -1rem;
  transition: all 0.2s;
  z-index: 1;
  color: #262322;
  margin: 0;
  font-size: 1.2rem;
}
.overlay .container .container_input .label-login::before {
  content: "";
  height: 0.5rem;
  background-color: #F2E5D7;
  position: absolute;
  left: 0;
  top: 1rem;
  width: 100%;
  z-index: -1;
}
.overlay .container .container_input .input-login {
  padding: 0.8rem;
  width: 100%;
  border: 2px solid #262322;
  border-radius: 0.5rem;
  font-size: 1.3rem;
  outline: none;
  transition: all 0.3s;
  color: #262322;
  background-color: #F2E5D7;
}
.overlay .container .container_input .input-login:focus {
  border: 3px solid;
}
.overlay .container .container_input .input-login::placeholder {
  font-size: 1.6rem;
  opacity: 0;
  transition: all 0.3s;
}
.overlay .container .container_input .input-login:focus::placeholder {
  opacity: 1;
}
.overlay .container .container_input .input-login:focus + .label-login, .overlay .container .container_input .filled {
  top: -1rem;
  font-size: 1.2rem;
}
.overlay .container .container_input span {
  color: red;
}
.overlay .container button {
  background-color: red;
  width: 100%;
  padding: 0.5rem;
  font-weight: bold;
  font-size: 1.4rem;
  cursor: pointer;
  border-radius: 0.5rem;
}
.overlay .container .decorate-span span {
  position: absolute;
  font-weight: bold;
  background-color: #F2E5D7;
  padding: 0 0.5rem;
  left: 50%;
  margin: -0.8rem 0 0 -1rem;
}
.overlay .container .decorate-span hr {
  border: 1px solid red;
  width: 90%;
}
.overlay .container .container-social {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin: 1rem auto;
  width: 90%;
}
.overlay .container .container-social .contain-instagram, .overlay .container .container-social .contain-google {
  display: grid;
  grid-template-columns: 0.5fr 2fr;
  gap: 0;
  border-radius: 0.5rem;
  border: 2px solid red;
  justify-content: center;
  cursor: pointer;
}
.overlay .container .container-social .contain-instagram .pic-instagram, .overlay .container .container-social .contain-instagram .pic-google, .overlay .container .container-social .contain-google .pic-instagram, .overlay .container .container-social .contain-google .pic-google {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: red;
  padding: 0.5rem;
}
.overlay .container .container-social .contain-instagram .pic-instagram img, .overlay .container .container-social .contain-instagram .pic-google img, .overlay .container .container-social .contain-google .pic-instagram img, .overlay .container .container-social .contain-google .pic-google img {
  width: 1.5rem;
  height: 1.5rem;
}
.overlay .container .container-social .contain-instagram span, .overlay .container .container-social .contain-google span {
  display: flex;
  justify-content: center;
  align-items: center;
}